<template>
    <yd-layout>
        <div class="feedbackForm">
            <div class="header">
                <div class="h_info">
                    <div class="name">发起人：{{getUserInfo.empname}}</div>
                    <div class="phone">办公室：{{getUserInfo.otel}}</div>
                </div>
                <div class="time">时间：{{date}}</div>
            </div>
            <div class="problemType">
                <div class="typeTitle">问题类型</div>
                <div class="typeOne">
                    <div class="onePro" @click="slide('typeOne');leaderIDs=leaderIDs_a;leaderName=leaderName_a">
                        <span>选项1：无法登录/绑卡/注册/显示二维码</span>
                        <img src="../../../static/images/arrow@.png" class="arrowImg" :class="{arrowClick:slideType}"/>
                    </div>
                        <div class="prodel" id="typeOne" :class="{prodelActive:slideType}">
                            <yd-cell-group>
                                <yd-cell-item>
                                    <span slot="left" class="leftName">手机号：</span>
                                    <yd-input slot="right" v-model="phone_a" ref="phone_a" regex="mobile" required :show-success-icon="false" :show-error-icon="false" placeholder="请输入手机号"></yd-input>
                                </yd-cell-item>
                                <yd-cell-item>
                                    <span slot="left" class="leftName">卡&nbsp&nbsp&nbsp号：</span>
                                    <yd-input slot="right" v-model="card_a" placeholder="请输入卡号"></yd-input>
                                </yd-cell-item>
                            </yd-cell-group>
                            <div class="pro">问题描述：</div>
                            <trsTextarea placeholder="请填写您的问题描述..." v-model="description_a"></trsTextarea>
                        </div>
                    </div>
                    <div class="typeOne typeTwo">
                        <div class="onePro" @click="slide('typeTwo');leaderIDs=leaderIDs_b;leaderName=leaderName_b">
                            <span>选项2：支付失败</span>
                            <img src="../../../static/images/arrow@.png" class="arrowImg" :class="{arrowClick:slideType2}"/>
                    </div>
                            <div class="prodel" id="typeTwo" :class="{prodelActive:slideType2}">
                                <yd-cell-group>
                                    <yd-cell-item>
                                        <span slot="left" class="leftName">手机号：</span>
                                        <yd-input slot="right" v-model="phone_b" ref="phone_b" regex="mobile" required :show-success-icon="false" :show-error-icon="false" placeholder="请输入手机号"></yd-input>
                                    </yd-cell-item>
                                    <yd-cell-item>
                                        <span slot="left" class="leftName">卡&nbsp&nbsp&nbsp号：</span>
                                        <yd-input slot="right" v-model="card_b" placeholder="请输入卡号"></yd-input>
                                    </yd-cell-item>
                                    <yd-cell-item>
                                        <span slot="left" class="leftName">商户代码：</span>
                                        <yd-input slot="right" v-model="code_b" placeholder="请输入商户代码"></yd-input>
                                    </yd-cell-item>
                                    <yd-cell-item>
                                        <span slot="left" class="leftName">交易时间：</span>
                                        <yd-datetime class="monthDate" v-model="time_b" slot="right"></yd-datetime>
                                    </yd-cell-item>
                                    <yd-cell-item>
                                        <span slot="left" class="leftName">交易金额：</span>
                                        <yd-input slot="right" v-model="money_b" placeholder="请输入交易金额"></yd-input>
                                    </yd-cell-item>
                                </yd-cell-group>
                                <div class="pro">问题描述：</div>
                                <trsTextarea placeholder="请填写您的问题描述..." v-model="description_b"></trsTextarea>
                            </div>
                        </div>
                        <div class="typeOne typeThree">
                            <div class="onePro" @click="slide('typeThree');leaderIDs=leaderIDs_c;leaderName=leaderName_c">
                                <span>选项3：无法享受优惠</span>
                                <img src="../../../static/images/arrow@.png" class="arrowImg" :class="{arrowClick:slideType3}"/>
                    </div>
                                <div class="prodel" id="typeThree" :class="{prodelActive:slideType3}">
                                    <div class="threeUosp">对于线下手机闪付交易不能享受优惠的 请通过uosp提交至信总</div>
                                    <yd-cell-group>
                                        <yd-cell-item>
                                            <span slot="left" class="leftName">活动ID<span class="spanS">(选填)</span>：</span>
                                            <yd-input slot="right" v-model="activityid_c" placeholder="请输入活动ID"></yd-input>
                                        </yd-cell-item>
                                        <yd-cell-item>
                                            <span slot="left" class="leftName">商户名称：</span>
                                            <yd-input slot="right" v-model="name_c" placeholder="请输入商户名称"></yd-input>
                                        </yd-cell-item>
                                        <yd-cell-item>
                                            <span slot="left" class="leftName">门店名称：</span>
                                            <yd-input slot="right" v-model="shopname_c" placeholder="请输入门店名称"></yd-input>
                                        </yd-cell-item>
                                        <yd-cell-item>
                                            <span slot="left" class="leftName">交易卡号：</span>
                                            <yd-input slot="right" v-model="card_c" placeholder="请输入交易卡号"></yd-input>
                                        </yd-cell-item>
                                        <yd-cell-item>
                                            <span slot="left" class="leftName">交易金额：</span>
                                            <yd-input slot="right" v-model="money_c" placeholder="请输入交易金额"></yd-input>
                                        </yd-cell-item>
                                        <yd-cell-item>
                                            <span slot="left" class="leftName">交易时间：</span>
                                            <yd-datetime class="monthDate" v-model="time_c" slot="right"></yd-datetime>
                                        </yd-cell-item>
                                        <yd-cell-item>
                                            <span slot="left" class="leftName">手机号：</span>
                                            <yd-input slot="right" v-model="phone_c" ref="phone_c" regex="mobile" required :show-success-icon="false" :show-error-icon="false" placeholder="请输入手机号"></yd-input>
                                        </yd-cell-item>
                                        <yd-cell-item>
                                            <span slot="left" class="leftName">商户代码：</span>
                                            <yd-input slot="right" v-model="code_c" placeholder="请输入商户代码"></yd-input>
                                        </yd-cell-item>
                                    </yd-cell-group>
                                    <div class="pro">问题描述：</div>
                                    <trsTextarea placeholder="请填写您的问题描述..." v-model="description_c"></trsTextarea>
                                    <!-- <div class="photoUp">拍照上传</div>
                                    <img src="../../../static/images/photoup.png" class="photoImg"/> -->
                                </div>
                            </div>
                        </div>
                        <div class="assignee">
                            <div class="name assigName">受理人：</div>
                            <div v-text="leaderName"></div>
                        </div>
                        <div class="bottom" v-if="jsonData.backreasonTijiao!=''&&workId!=0">
                            <div class="botTitle">
                                处理结果反馈
                            </div>
                            <!-- <div class="record" v-if="jsonData.handleResult!=''">
                                <div class="nameSugg">
                                    <div class="name" v-if="jsonData.chuliName!=''">{{jsonData.chuliName}}：</div>
                                    <div class="suggest">{{jsonData.handleResult}}</div>
                                </div>
                                <div class="recorddate">{{jsonData.reqstartdate}}</div>
                            </div> -->
                            <div class="record" v-if="jsonData.backreasonTijiao!=''">
                                <div class="nameSugg">
                                    <div class="name" v-if="jsonData.backreasonTijiaoPerson!=''">{{jsonData.backreasonTijiaoPerson}}：</div>
                                    <div class="suggest" v-html="transferString(jsonData.backreasonTijiao)"></div>
                                </div>
                                <div class="recorddate">{{jsonData.tuihuiTime}}</div>
                            </div>
                        </div>
                    </div>
                </div>
                <yd-tabbar slot="tabbar" v-if="this.$route.params.status==0">
                    <div class="agree" @click="send()">发送</div>
                </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
import $ from '../../jquery-vendor';
export default {
    name: 'feedback',
    data() {
        return {
            slideType: false,
            slideType2: false,
            slideType3: false,
            title: '审批人',
            leaderName: '',
            leaderIDs: '',
            leaderName_a: '',
            leaderIDs_a: '',
            leaderName_b: '',
            leaderIDs_b: '',
            leaderName_c: '',
            leaderIDs_c: '',
            phone_a: '',
            card_a: '',
            phone_b: '',
            card_b: '',
            code_b: '',
            money_b: '',
            time_b: '',
            activityid_c: '',
            name_c: '',
            shopname_c: '',
            card_c: '',
            money_c: '',
            time_c: '',
            phone_c: '',
            code_c: '',
            date: '',
            ext: '',
            timeNum: 0,
            jsonData: {},
            description_a: '',
            description_b: '',
            description_c: '',
            workId: this.$route.params.workId
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('营销问题反馈');
        vm.queryLeader('a');
        vm.queryLeader('b');
        vm.queryLeader('c');
        vm.date = vm.formatDate(new Date(), 'yyyy-MM-dd hh:mm');
        vm.time_c = vm.time_b = vm.date;
    },
    watch: {
        timeNum: function() {
            let vm = this;
            if (vm.timeNum == 3 && vm.workId != 0) {
                vm.queryForm();
            }
        }
    },
    computed: {
        ...mapGetters([
            'getUserInfo'
        ])
    },
    destroyed() {
        this.timeNum = 0;
    },
    methods: {
        slide(id) {
            if (id == 'typeOne') {
                this.slideType = !this.slideType;
                this.slideType2 = false;
                this.slideType3 = false;
            } else if (id == 'typeTwo') {
                this.slideType = false;
                this.slideType2 = !this.slideType2;
                this.slideType3 = false;
            } else if (id == 'typeThree') {
                this.slideType = false;
                this.slideType2 = false;
                this.slideType3 = !this.slideType3;
            }
            $('.prodel').not($('#' + id)).slideUp();
            $('#' + id).slideToggle();
        },
        queryForm() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                'type': 'troubleshootingMain',
                'workItemID': vm.$route.params.workId,
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
            }, function(res) {
                if (res.jsonData.CURRENTSTATE != 10 && res.jsonData.CURRENTSTATE != 4 && vm.$route.params.status == 0 && res.isSuccess == 'true') {
                    vm.$dialog.toast({
                        mes: '该流程已被处理',
                        timeout: 2000,
                        callback: () => {
                            setTimeout(function() {
                                if (vm.$route.params.status == 0) {
                                    vm.$router.push('/handle');
                                }
                            })
                        }
                    });
                    if (vm.$route.params.status == 0) {
                        return false;
                    }
                }
                if (res.isSuccess === 'true') {
                    vm.jsonData = res.jsonData;
                    if (vm.jsonData.approvalitem == 'a') {
                        vm.leaderIDs = vm.leaderIDs_a;
                        vm.leaderName = vm.leaderName_a;
                        vm.slideType = true;
                        vm.phone_a = vm.jsonData.reqtelA;
                        vm.card_a = vm.jsonData.cardNumA;
                    } else if (vm.jsonData.approvalitem == 'b') {
                        vm.leaderIDs = vm.leaderIDs_b;
                        vm.leaderName = vm.leaderName_b;
                        vm.slideType2 = true;
                        vm.phone_b = vm.jsonData.reqtelB;
                        vm.card_b = vm.jsonData.cardnumB;
                        vm.code_b = vm.jsonData.merchantCodeB;
                        vm.money_b = vm.jsonData.jiaoyiMoneyB;
                        vm.time_b = vm.jsonData.jioayiTimeB.substring(0, vm.jsonData.jioayiTimeB.length - 3);
                    } else if (vm.jsonData.approvalitem == 'c') {
                        vm.leaderIDs = vm.leaderIDs_c;
                        vm.leaderName = vm.leaderName_c
                        vm.slideType3 = true;
                        vm.phone_c = vm.jsonData.reqtelC;
                        vm.card_c = vm.jsonData.jioayiCardC;
                        vm.code_c = vm.jsonData.merchantCodeC;
                        vm.money_c = vm.jsonData.jiaoyiMoneyC;
                        vm.time_c = vm.jsonData.jiaoyiTimeC.substring(0, vm.jsonData.jiaoyiTimeC.length - 3);
                        vm.shopname_c = vm.jsonData.shopNameC;
                        vm.name_c = vm.jsonData.merchantNameC;
                        vm.activityid_c = vm.jsonData.activityidC;
                    }
                }
            })
        },
        queryLeader(type) {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryWTCLHandlerPerson', {
                'queryType': type,
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
            }, function(res) {
                vm.timeNum++;
                if (type === 'a') {
                    let arrAname = [];
                    let arrAid = [];
                    res.jsonData.map((item) => {
                        arrAname.push(item.empname);
                        arrAid.push(item.operatorid);
                    });
                    vm.leaderName_a = arrAname.join(',');
                    vm.leaderIDs_a = arrAid.join(',');
                } else if (type === 'b') {
                    let arrBname = [];
                    let arrBid = [];
                    res.jsonData.map((item) => {
                        arrBname.push(item.empname);
                        arrBid.push(item.operatorid);
                    });
                    vm.leaderName_b = arrBname.join(',');
                    vm.leaderIDs_b = arrBid.join(',');
                } else if (type === 'c') {
                    let arrCname = [];
                    let arrCid = [];
                    res.jsonData.map((item) => {
                        arrCname.push(item.empname);
                        arrCid.push(item.operatorid);
                    });
                    vm.leaderName_c = arrCname.join(',');
                    vm.leaderIDs_c = arrCid.join(',');
                }
            })
        },
        send() {
            let vm = this;
            if (vm.slideType) {
                vm.ext = '{"requserid":"' + vm.getUserInfo.userid + '","requsername":"' + vm.getUserInfo.empname + '","reqname":"营销问题处理流程","approvalitem":"a","reqremark":"","reqtel_a":"' + vm.phone_a + '","cardnum_a":"' + vm.card_a + '","description":"' + vm.description_a + '"}';
                if (vm.phone_a == '') {
                    vm.$dialog.toast({
                        mes: '请输入手机号',
                        timeout: 2000
                    });
                } else if (!vm.$refs.phone_a.valid) {
                    vm.$dialog.toast({
                        mes: '请输入正确的手机号',
                        timeout: 2000
                    });
                } else if (vm.card_a == '') {
                    vm.$dialog.toast({
                        mes: '请输入卡号',
                        timeout: 2000
                    });
                } else if (vm.description_a == '') {
                    vm.$dialog.toast({
                        mes: '请输入您的问题描述',
                        timeout: 2000
                    });
                } else {
                    vm.doSend();
                }
            } else if (vm.slideType2) {
                vm.time_b = vm.time_b + ':00';
                vm.ext = '{"requserid":"' + vm.getUserInfo.userid + '","requsername":"' + vm.getUserInfo.empname + '","reqname":"营销问题处理流程","approvalitem":"b","reqremark":"","reqtel_b":"' + vm.phone_b + '","cardnum_b":"' + vm.card_b + '","merchantcode_b":"' + vm.code_b + '","jiaoyitime_b":"' + vm.time_b + '","jiaoyimoney_b":"' + vm.money_b + '","description":"' + vm.description_b + '"}';
                if (vm.phone_b == '') {
                    vm.$dialog.toast({
                        mes: '请输入手机号',
                        timeout: 2000
                    });
                } else if (!vm.$refs.phone_b.valid) {
                    vm.$dialog.toast({
                        mes: '请输入正确的手机号',
                        timeout: 2000
                    });
                } else if (vm.card_b == '') {
                    vm.$dialog.toast({
                        mes: '请输入卡号',
                        timeout: 2000
                    });
                } else if (vm.code_b == '') {
                    vm.$dialog.toast({
                        mes: '请输入商户代码',
                        timeout: 2000
                    });
                } else if (vm.money_b == '') {
                    vm.$dialog.toast({
                        mes: '请输入交易金额',
                        timeout: 2000
                    });
                } else if (vm.description_b == '') {
                    vm.$dialog.toast({
                        mes: '请输入您的问题描述',
                        timeout: 2000
                    });
                } else {
                    vm.doSend();
                }
            } else if (vm.slideType3) {
                vm.time_c = vm.time_c + ':00';
                vm.ext = '{"requserid":"' + vm.getUserInfo.userid + '","requsername":"' + vm.getUserInfo.empname + '","reqname":"营销问题处理流程","approvalitem":"c","reqremark":"","merchantcode_c":"' + vm.code_c + '","jiaoyitime_c":"' + vm.time_c + '","jiaoyimoney_c":"' + vm.money_c + '","reqtel_c":"' + vm.phone_c + '","activityid_c":"' + vm.activityid_c + '","merchantname_c":"' + vm.name_c + '","jiaoyicard_c":"' + vm.card_c + '","shopname_c":"' + vm.shopname_c + '","description":"' + vm.description_c + '"}';
                if (vm.name_c == '') {
                    vm.$dialog.toast({
                        mes: '请输入商户名称',
                        timeout: 2000
                    });
                } else if (vm.shopname_c == '') {
                    vm.$dialog.toast({
                        mes: '请输入门店名称',
                        timeout: 2000
                    });
                } else if (vm.card_c == '') {
                    vm.$dialog.toast({
                        mes: '请输入交易卡号',
                        timeout: 2000
                    });
                } else if (vm.money_c == '') {
                    vm.$dialog.toast({
                        mes: '请输入交易金额',
                        timeout: 2000
                    });
                } else if (vm.phone_c == '') {
                    vm.$dialog.toast({
                        mes: '请输入手机号',
                        timeout: 2000
                    });
                } else if (!vm.$refs.phone_c.valid) {
                    vm.$dialog.toast({
                        mes: '请输入正确的手机号',
                        timeout: 2000
                    });
                } else if (vm.code_c == '') {
                    vm.$dialog.toast({
                        mes: '请输入商户代码',
                        timeout: 2000
                    });
                } else if (vm.description_c == '') {
                    vm.$dialog.toast({
                        mes: '请输入您的问题描述',
                        timeout: 2000
                    });
                } else {
                    vm.doSend();
                }
            }
        },
        doSend() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                'type': 'troubleshootingMain',
                'workItemID': vm.workId,
                'personID': vm.getUserInfo.userid,
                'activityID': 'handleResultActivity',
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                'leaderIDs': vm.leaderIDs,
                'workTitle': vm.getUserInfo.deptname + vm.getUserInfo.empname + '营销问题反馈',
                'ext': vm.ext
            }, function(res) {
                if (res.isSuccess == 'true') {
                    vm.$dialog.toast({
                        mes: '发送成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle');
                        }
                    });
                }
            });
        }
    }
}
</script>
<style lang="less" scoped>
@import 'feedback.less';
.threeUosp {
    font-size: .3rem;
    color: #333;
    padding-left: .24rem;
    line-height: .7rem;
}
.assigName {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
}
// 底部按钮
.agree {
    flex: 1;
    color: #fff;
    font-size: .36rem;
    line-height: 1rem;
    text-align: center;
}

.agree {
    background-color: #59a5fd;
}

.monthDate {
    font-size: .3rem;
}

.bottom {
    // padding: 0 .13rem;
    background-color: #fff;

    .botTitle {
        height: 1rem;
        line-height: 1rem;
        font-size: .32rem;
        color: #333;
        font-weight: bold;
        padding: 0 .34rem;
        border-bottom: 1px dashed #eaeaea;
    }

    .record {
        overflow: hidden;
    }

    .nameSugg {
        // display: flex;
        font-size: .3rem;
        color: #666;
        padding: 0 .34rem;
        padding-top: .4rem;
    }

    .name {
        margin-bottom: .28rem;
    }

    .suggest {
        padding: .27rem .31rem;
        border: 1px solid #cdcdcd;
    }

    .recorddate {
        color: #999;
        width: 100%;
        padding: 0 .34rem;
        padding-top: .3rem;
        padding-bottom: .3rem;
        font-size: .3rem;
    }

    .myfq {
        color: #666;
    }
}
</style>
<style lang="less">
.feedbackForm {
    .yd-cell-item:not(:last-child):after {
        background-image: none;
    }

    input {
        font-size: .3rem !important;
    }

    input::-webkit-input-placeholder {
        font-size: .32rem !important;
    }

    input::-ms-input-placeholder {
        font-size: .32rem !important;
    }

    input::-moz-placeholder {
        font-size: .32rem !important;
    }
    .pro {
        font-size: .3rem;
        color: #333;
        padding-left: .24rem;
        height: 1rem;
        line-height: 1rem;
    }
    .trsTextarea {
        padding-left: .24rem;
        font-size: .3rem;
        min-height: 2rem;
        padding-right: .24rem;
        padding-bottom: .2rem;
    }
}
</style>


